// 
// Styles for PortfolioCard
//
const styles = theme => ({
  card: {
    display: "inline-block",
    float: "left",
    width: "35vw",
    maxHeight: "80vh",
    marginLeft: "5%",
    marginBottom: "2%",
    paddingBottom: "1%",
    background: "#fafcfe",
  },
  root: {
    paddingBottom: "1%",
  },
  cardHeader: {
    padding: "2%",
  },
  cardContent: {
    display: "inline-block",
    padding: "1%  1%",
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  actions: {
    display: 'flex',
    padding: '0px',
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  chip: {
    // margin: theme.spacing.unit,
    margin: "0.2em",
    fontSize: "0.75em",
    height: "1.6em",
    variant: "outlined",
  },
  link: {
    margin: theme.spacing.unit,
    color: 'inherit',
    width: "5%", 
    height: "5%", 
    marginLeft: "5%",
  },
  button: {
    margin: theme.spacing.unit,
    padding: "0% 2%",
    fullWith: true,
    size: "small",
    mini: true,
  },
});

export default styles;