// 
// Styles for AboutMe component
//
const styles = theme => ({
  root: {
    margin: theme.spacing.unit,
    marginLeft: '5%',
    marginRight: '5%',
  },
  article: {
    fontSize: "1.0em",
    color: "#777",
    lineHeight: "2.0em",
    animationName: "text-fadein",
    animationDuration: "4s",
  },
  avatar: {
    margin: 10,
    width: 160,
    height: 160,
    float: "left",
  },
  photo: {  // deprecated by avatar
    width: "15%",
    float: "left",
    margin: "10px 15px 0px 0px",
  },
  logoDarkBG: {
    width: "15%",
    height: "10%",
    margin: "0% 0%",
    background: "#333",
  },
  divider: {
    margin: "2% 0% 1% 0%",
    clear: "left",
  },
  link: {
    margin: theme.spacing.unit,
    color: '#555',
    fontSize: "1.2em",
  },
  skills: {
    display: "block",
    width: "100%",
    fontSize: "1.2em",
  },
  tech: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
  },
  chip: {
    margin: theme.spacing.unit,
  },
});

export default styles;