import React from 'react';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { blue } from '@material-ui/core/colors';

import aboutMe from '../config/aboutMe';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  left: {
    alignItems: "flex-start",
    flexGrow: 11,
  },
  bar: {
    backgroundColor: blue[300],
  },
  menuButton: {
    // marginLeft: -18,
    // marginRight: 10,
    alignContent: "flex-end",
  },
  button: {
    margin: theme.spacing.unit,
  },
});

/**
 * App navigation bar component at page top
 * 
 * @param {object} props
 */
function DenseAppBar(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.bar}>
        <Toolbar variant="regular">
          <Typography variant="h6" color="inherit" className={classes.grow}>
            {aboutMe.name}
          </Typography>
          <Typography variant="subtitle1" color="inherit" className={classes.left}>
            {pageSubtitle(props)}
          </Typography>
          <Typography variant="subheading" color="inherit">
            <Button 
              variant="outlined" 
              color="inherit" 
              href={"/#/" + pageMenu(props).toLowerCase()} 
              className={classes.button}>
              {pageMenu(props)}
            </Button>
          </Typography>
          {menuButton(props)}
        </Toolbar>
      </AppBar>
    </div>
  );
}

//
// Identify the current location from the router
//
function currentLocation(props) {
  const pathname = props.location.pathname;

  if (pathname === '/') {
    return "about";
  } else if (pathname === '/portfolio') {
    return "portfolio";
  }

  return "about";
}

//
// Update page sub-title in the nav-bar per location
//
function pageSubtitle(props) {
  let subtitle = "";
  const location = currentLocation(props);

  if (location === 'about') {
    // subtitle = "About Me";
    subtitle = "";
  } else if (location === 'portfolio') {
    subtitle = "portfolio"
  }

  return subtitle;
}

//
// Update page menu in the nav-bar per location
//
function pageMenu(props) {
  let subtitle = "";
  const location = currentLocation(props);

  if (location === 'about') {
    subtitle = "Portfolio";
  } else if (location === 'portfolio') {
    subtitle = "About"
  }

  return subtitle;
}

function menuButton(props) {
  // const { classes } = props;
  const location = currentLocation(props);

  if (location === "portfolio") {
    // Menu for filtering projects by technology
    return <TechMenu {...props} />;
  } else {
    return;
  }
}

/**
 * Menu for filtering projects by technology
 */
function TechMenu(props) {
  const { classes } = props; 
  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  function handleMenuSelection(techName) {
    setAnchorEl(null);
    props.selectTech(techName);
  }

  return (
    <div>
      <IconButton className={classes.menuButton} color="inherit" aria-label="Menu" onClick={handleClick}>
        <MenuIcon />
      </IconButton>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={() => handleMenuSelection(null)}>All Technologies</MenuItem>
        {props.technologies.map((tech, i) => (
          <MenuItem
            key={i.toString()}
            onClick={() => handleMenuSelection(tech)}>
            {tech}
          </MenuItem>
        ))}  
      </Menu>
    </div>
  );
}

DenseAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  projects: PropTypes.array.isRequired,
};

export default withStyles(styles)(withRouter(DenseAppBar));
