import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import Link from '@material-ui/core/Link';

const styles = theme => ({
  root: {
    maxWidth: 600,
    flexGrow: 1,
    margin: "auto",
    display: "center",
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    height: "auto",
    paddingLeft: theme.spacing.unit * 4,
    backgroundColor: theme.palette.background.default,
    fontSize: "1.5em",
  },
  img: {
    height: 375,
    maxWidth: 600,
    overflow: 'hidden',
    display: 'block',
    width: '100%',
  },
  link: {
    margin: theme.spacing.unit,
    color: '#555',
    fontSize: "1.2em",
  },
});

class TextMobileStepper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
  }

  handleNext = () => {
    this.setState (prevState => ({
      activeStep: prevState.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState (prevState => ({
      activeStep: prevState.activeStep - 1,
    }));
  };

  render () {
    const {classes, theme } = this.props;
    const {activeStep} = this.state;
    const maxSteps = Math.min(this.props.projects.length, 3); 

    return (
      <div className={classes.root}>
        <Paper square elevation={0} className={classes.header}>
          <Typography style={{fontSize: "1.1em", color: "#7ab", margin: "0px 0px"}}>
            {this.props.projects[activeStep].title}
          </Typography>
        </Paper>
        <Paper square elevation={0} className={classes.header}>
          <Typography style={{fontSize: "0.7em", color: "#555", margin: "0px 7%"}}>
            {this.props.projects[activeStep].description}
          </Typography>
        </Paper>
        <Link
          target="_self"
          rel="noopener noreferrer"
          href="/#/portfolio"
          className={classes.tech}
        >
          <img
          className={classes.img}
          src={this.props.projects[activeStep].image}
          alt={this.props.projects[activeStep].name}
          />
        </Link>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          className={classes.mobileStepper}
          nextButton={
            <Button
              size="small"
              onClick={this.handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              Next
              {theme.direction === 'rtl'
                ? <KeyboardArrowLeft />
                : <KeyboardArrowRight />}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={this.handleBack}
              disabled={activeStep === 0}
            >
              {theme.direction === 'rtl'
                ? <KeyboardArrowRight />
                : <KeyboardArrowLeft />}
              Back
            </Button>
          }
        />
      </div>
    );
  }
}

TextMobileStepper.propTypes = {
  projects: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, {withTheme: true})(TextMobileStepper);
