/**
 * About-Me page component
 * 
 * - 3 sections
 *   1)About Me
 *   2)Skills
 *   3)Web Applications [3 samples]
 * 
 */

import React from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Chip from '@material-ui/core/Chip';
import Link from '@material-ui/core/Link';

import styles from './styles/AboutMe';
import TextMobileStepper from './TextMobileStepper';
import Footer from './Footer';
import aboutMe from '../config/aboutMe';
import skills from '../config/skills';


/**
 * Main AboutMe function
 * 
 * @param {object} props
 */
const AboutMe = props => {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Typography className={classes.skills}>
        About Me
      </Typography>
      <aside>
        <Avatar
          src={aboutMe.avatar.image}
          alt={aboutMe.avatar.alt}
          className={classes.avatar}
        />
      </aside>

      <article>
        {
          aboutMe.paragraphs.map((p, i) =>
            <div key={i.toString()}>
              <Typography className={classes.article}>
                {p}
              </Typography>
            </div>
          )
        }
      </article>

      <Divider className={classes.divider} />

      <section className={classes.tech}>
        <Typography className={classes.skills}>
          Skills
        </Typography>
        <SkillList {...props} />
      </section>
      <Divider className={classes.divider} />

      <section>
        <Typography className={classes.skills}>
          Web Applications
        </Typography>
        <TextMobileStepper projects={props.projects} />
        <Link href="/#/portfolio" className={classes.tech}>
          <Typography color="primary">
            See more on the portfolio page
          </Typography>
        </Link>
      </section>

      <Divider className={classes.divider} />
      <footer>
          <Footer />
      </footer>

    </div>
  )
};

/**
 * Populate Skills section with names of skills and tecnologies
 * 
 * @param {object} props 
 */
const SkillList = props => {
  if (skills.useChip) {
    return skills.list.map((skill, i) => (
      <div key={i.toString()}>
        <SkillChipItem skill={skill} {...props} />
      </div>
    ));  
  } else {
    return skills.list.map((skill, i) => (
      <div key={i.toString()}>
        <SkillItem skill={skill} {...props} />
      </div>
    ));  
  }
};


/**
 * Individual skill item in an SVG oval shape
 * https://www.w3schools.com/html/html5_svg.asp
 * 
 * NOTE: Fixed size ellipse for all skill string lengths
 * 
 * @param {object} props 
 */
const SkillItem = props => {
  let textLen = 75;
  let xPos = 15;
  
  if (props.skill.length < 5) {
    textLen = 45;
    xPos = 29;
  }

  return (
    <svg height="45" width="120">
      <defs>
        <linearGradient id="grad1" x1="0%" y1="0%" x2="100%" y2="70%">
          <stop offset="25%" style={{ stopColor: "rgb(165,205,255)", stopOpacity: 1 }} />
          <stop offset="100%" style={{ stopColor: "rgb(0,155,255)", stopOpacity: 1 }} />
        </linearGradient>
      </defs>
      <ellipse cx="55" cy="25" rx="55" ry="15" fill="url(#grad1)" />
      <text fill="#ffffff" fontSize="16" fontFamily="Verdana" 
            x={xPos} y="31" textLength={textLen} lengthAdjust="spacing">
        {props.skill}
      </text>
      Sorry, your browser does not support inline SVG.
    </svg>
  );
};

/**
 * Individual skill item in a chip
 * 
 * @param {object} props 
 */
const SkillChipItem = props => {
  const { skill, classes } = props;
  return <Chip 
    label={skill} 
    className={classes.chip} 
    color="primary" 
    variant="outlined"
  />
};

AboutMe.propTypes = {
  projects: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AboutMe);
