/**
 * Footer component
 * 
 * - Reads configuration from ../config/contactLinks.js 
 * - Example:
 *   {
 *     name: "LinkedIn", 
 *     href: "https://www.linkedin.com/",
 *     icon: { class: "fa fa-linkedin-square fa-1g", ariaHidden: "true" },
 *   }
 * 
 */

import React from 'react'
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { withStyles } from '@material-ui/core/styles';
import contactLinks from "../config/contactLinks";

// 
// For styles
//
const styles = theme => ({
  root: {
    marginLeft: "auto",
    marginRight: "auto",
    marginBottom: "7%",
    width: "40%",
    display: "block",
    textAlign: "center",
  },
  link: {
    margin: theme.spacing.unit,
    color: '#555',
    fontSize: "1.2em",
    textAlign: "center",
  },
});

/**
 * Footer component
 * 
 * @param {object} props 
 */
function Footer(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Typography className="links">
        <SocialLinks {...props} />
      </Typography>
    </div>
  )
}

/**
 * Populate social link elements
 * 
 * @param {object} props 
 */
const SocialLinks = props => {
  return contactLinks.map((link, i) => (
    <LinkItem key={i.toString()} {...link} {...props} />
  ));
};

/**
 * Single link item component
 * - Open a target link in a new tab except an email link
 * 
 * @param {object} props 
 */
const LinkItem = props => {
  const target = (/Email/i.test(props.name)) ? "_top" : "_blank";

  return (
    <Link
      className={props.classes.link}
      href={props.href} 
      rel="noopener noreferrer" 
      target={target}>
      <i className={props.icon.class} aria-hidden={props.icon.ariaHidden}></i>
      {props.name}
    </Link>
  );
}

export default withStyles(styles)(Footer);
