/**
 * Main entry point
 * 
 * - Two pages
 *   1)AboutMe
 *   2)Portfolio
 * - Using HashRouter for stand-alone Ract page on GitHub
 * 
 */

import React, { Component } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import AppBar from './AppBar';
import AboutMe from './AboutMe';
import Portfolio from './Portfolio';
import projects from '../config/projects';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      projects: this.initProjects(),
    };
    this.technologies = this.uniqTechnologies();
    this.handleCardExpandClick = this.handleCardExpandClick.bind(this);
  }

  /**
   * Initialize the state of projects array
   * - "projects" contains info for cards in the portfolio page.  
   * - Initialize all cards' expanded state to be false
   */
  initProjects() {
    return projects.slice().map(prj => {
      prj.expanded = false;
      return prj;
    });
  }

  /**
   * Handle a click event to expand/fold a project card
   */
  handleCardExpandClick(i) {
    const prj = this.state.projects.slice();
    
    prj[i].expanded = !this.state.projects[i].expanded;
    this.setState({ projects: prj });
  }

  /**
   * Get a list of unique technologies from projects
   * 
   * @return {array}
   */
  uniqTechnologies() {
    let techSet = new Set();

    projects.forEach(prj => {
      for(let tech of prj.technologies) {
        techSet.add(tech);
      }
    });

    return Array.from(techSet);
  };

  /**
   * Upon technology selection, handle filtering projects
   */
  selectTechnology(techName) {
    let projectList = projects.slice();
    
    if (techName) {
      projectList = projectList.filter(prj => {
        return prj.technologies.includes(techName);
      });  
    }
    
    this.setState({ projects: projectList });
  }

  /**
   * Render a page; router handles switching btw pages
   *   1)AboutMe
   *   2)Portfolio
   */
  render() {
    return (
      <Router>
        <nav>
          <AppBar
            projects={this.state.projects}
            technologies={this.technologies}
            selectTech={tech => this.selectTechnology(tech)}
          />
        </nav>

        <section>
          <Switch>
            <Route
              exact path="/portfolio"
              render={props => 
                <Portfolio
                  projects={this.state.projects}
                  handleCardExpandClick={i => this.handleCardExpandClick(i)}
              />}
            />
            <Route
              path="/"
              render={props => 
                <AboutMe
                  // projects={this.state.projects}
                  projects={projects}
                  {...props}
                />}
            />
          </Switch>
        </section>
      </Router>
    )
  }
}

export default Main;
